import React from 'react'
import './styles.css'
import TextStats from '../TextStats'
import Interval from '../Interval'
import httpError from '../httpError'

import Progress from 'react-progress'
import axios from 'axios'
import Cookies from 'js-cookie'

class LeadsBy extends React.Component {
  constructor(props) {
    super(props);
    this.state = { stats: [], overMissed:0, progress:0, interval: {} }

    this.onInterval = this.onInterval.bind(this)    
  }

  componentDidMount() {
    const fromLocalStorage = localStorage.getItem('leadsBy')
    if (fromLocalStorage) this.setState(JSON.parse(fromLocalStorage))    
    this.setState({ msid: Cookies.get('msid') }, () => this.fetchInfo())
  }

  fetchInfo() {
    this.setState({ progress: 7 })
    const delay = Math.floor(Math.random() * (100 - 10 + 1)) + 10
    const progress = Math.floor(Math.random() * (25 - 10 + 1)) + 10
    setTimeout(() => this.setState({ progress  }), delay)

    const { msid } = this.state
    let url = `http://papi.mindsales-crm.com/stats/leads?token=${msid}`

    axios.get(url)
      .then(({ data: { stats } }) => {
        this.setState({ progress: 100 })
        this.setState({ stats })
        localStorage.setItem('leadsBy', JSON.stringify({ stats }))
      })
      .catch(httpError);    
  }    

  onInterval(interval) {
    this.setState({ interval }, () => this.fetchInfo());    
  }  

  drawManagers() {
    const { stats } = this.state
    return stats.length > 0?
      stats.map(({ manager, count}, index) => <TextStats key={index} title={manager} value={count} />)
      :  
      (<div>Данных нет</div>)
  }
  
  render(){
    return (
      <div className="bContent">
        <Progress style={{boxShadow:'none'}} percent={this.state.progress} color="#D29FCD" height="12" />
        <h1>Необработанные лиды</h1>
        <div className="innerContent">
          { this.drawManagers() }        
        </div>
      </div>   
    )
  }
}  

export default LeadsBy



